import React from 'react';
import Section from '../../../components/Section';
import TextHighlight from '../../../components/TextHighlight';
import Code from '../../../components/Code';
import * as codes from '../../../resources/restcodes';

export default class DeleteDocument extends React.Component {

    render() {

        return (
            <Section>
                <h1 id="/rest/guides/datastore/delete/document">Delete Document</h1>
                <p>
                    You have saved the document in datastore and now you want to delete it. You just have to send a
                    <TextHighlight>DELETE</TextHighlight> request to its object URL along with the
                    <TextHighlight>_id</TextHighlight> of the document as a <TextHighlight>request path
                    variable</TextHighlight>. For example:
                </p>

                <Code>
                    {codes.deleteDocument}
                </Code>

                <Code>
                    {codes.deleteDocumentReponse}
                </Code>

                <p>
                    Direct children of this document with a cascade property <TextHighlight>all</TextHighlight> or
                    <TextHighlight>delete</TextHighlight> will also be deleted.
                </p>
            </Section>
        )
    }
}



