import React from 'react';
import Section from '../../../components/Section';
import TextHighlight from '../../../components/TextHighlight';
import Code from '../../../components/Code';
import * as codes from '../../../resources/restcodes';

export default class ManyToOne extends React.Component {

    render() {
        return (
            <Section>
                <h2 id="/rest/guides/datastore/relations/many-one">ManyToOne</h2>
                <p>
                    In <TextHighlight>One-To-Many</TextHighlight> relationship, references to child documents will be
                    saved in an array under the key
                    on which relationship has been mentioned in the parent document. Now while retrieving the document,
                    if you would also like to fetch child documents, the performance metric for parent document might
                    get affected because of the large collection of references stored in the array, and datastore would
                    have to go through each and every reference stored in the array.
                </p>

                <p>
                    To resolve this issue, documents can have a reverse relationship of
                    <TextHighlight>One-to-Many</TextHighlight>. If one <TextHighlight>user</TextHighlight> can have
                    multiple <TextHighlight>notes</TextHighlight>, similarly multiple notes could be saved by a single
                    <TextHighlight>user</TextHighlight> which makes the
                    relationship between notes and user <TextHighlight>Many-to-One</TextHighlight>. Below is the sample
                    JSON object to achieve the
                    same.
                </p>

                <Code>
                    {codes.manyToOneJson}
                </Code>
                <p>
                    Relationships <TextHighlight>One-to-One</TextHighlight> and
                    <TextHighlight>Many-to-One</TextHighlight> could look the same but should not be confused with. If
                    used in a replaceable manner, it could affect the retrieval of documents.
                </p>
            </Section>
        )
    }
}



