import Section from '../../../components/Section';
import Callout from '../../../components/Callout';
import React from 'react';

export default class QuickStart extends React.Component {

    render() {
        return (
            <Section>
                <h1 id="/docs/rest/guides/start">Quick Start</h1>
                <p>
                    Vostics Cloud is an automated cloud computing platform providing end-to-end solutions for your
                    project whether it is a Mobile app, Web app or Desktop app.
                    <br/>
                    <br/>
                    Our platform offers products required by all modern apps, like a pre-hosted database, user
                    authentication, file storage, and messaging. All of these are accessible either via REST APIs or
                    through our Client SDKs.
                </p>

                <Callout>
                    All our SDKs including the alpha version i.e. v-x.x.x-SNAPSHOT are fully tested and ready to use
                    with minimal configuration in production version also.
                </Callout>

                <p>Products at the alpha stage are free of any pricing schemes and are not bound by an SLA.</p>

                <h2 id="/rest/guides/start/console">Developer Console</h2>
                <p>
                    Manage all your projects from our Developer Console. On Vostics Cloud, a project is created for each
                    one of your application. The Developer Console allows you to control all aspects of your project
                    along with the project security and client accessibility etc.
                </p>

                <h2 id="/rest/guides/start/docs">Documentation</h2>
                <p>
                    Documentation is available for the REST APIs and all of the SDKs. The documentation will give you a
                    detailed view of all products and how to use them. API documentation is also available for each
                    client SDK.
                </p>
            </Section>
        )
    }
}



