import React from 'react';
import Callout from '../../../components/Callout';
import TextHighlight from '../../../components/TextHighlight';
import Section from '../../../components/Section';
import Table from '../../../components/Table';

export default class Relations extends React.Component {

    relationshipProps = [
        ['relation', 'Type of relationship between documents.'],
        ['documentName', 'Name of the child document under which child document will be saved.'],
        ['embed', <div>
            <TextHighlight>true</TextHighlight> if you want to embed document else <TextHighlight>
            false.</TextHighlight>
        </div>],
        ['cascadeType', <div>Cascading is the actions to be taken on child document(s) when <TextHighlight>
            saving </TextHighlight>, <TextHighlight> updating </TextHighlight> or
            <TextHighlight>deleting</TextHighlight>
            the parent document.</div>],
        ['joinDocument',
            <div>
                Name of the document where references to <TextHighlight> many-to-many</TextHighlight> relationships will
                be stored.
            </div>
        ],
        ['joinKey',
            <div>
                Name of key under which reference to parent document for the <TextHighlight>
                many-to-many </TextHighlight>
                relationship will be stored
            </div>
        ],
        ['inverseJoinKey',
            <div>
                Name of the key under which reference to child document for the
                <TextHighlight>many-to-many</TextHighlight> relationship will be stored
            </div>
        ]
    ];

    relations = [
        ['oneToOne', 'Maps one parent document with single child document'],
        ['oneToMany', 'Maps one parent document with multiple child documents'],
        ['manyToOne', 'Reverse mapping for oneToMany relationship. Maps multiple child documents with single parent document'],
        ['manyToMany', 'Maps multiple child documents with multiple parent documents and stores there reference in a different table']
    ];

    cascading = [
        ['none', 'No operation is performed on child document'],
        ['save', 'Saves the child document with parent document'],
        ['save_update', 'Updates the child document when updating the parent document'],
        ['update', 'Saves and updates the child document when saving and updating parent document'],
        ['delete', 'Deletes the child document if the parent document is deleted'],
        ['all', 'Performs all above operations on child document']
    ];

    render() {
        return (
            <Section>
                <h1 id="/rest/guides/datastore/relations">Relations</h1>
                <p>
                    Most of the time when you are saving documents you would want to define the relationship between the
                    documents.
                </p>

                <p>
                    Mapping of documents can be achieved in two ways in Datastore: either by <TextHighlight>linking
                    two</TextHighlight> documents using a <TextHighlight>foreign key</TextHighlight> or by
                    <TextHighlight>embedding</TextHighlight> one document into another.
                </p>
                <p>
                    Embedding is used when the number of child documents count is less (say around 50); but if child
                    documents count is more than 50, then linking parent and child document would be a more suitable
                    option.
                </p>
                <p>Remember embedding and linking have their own performance metrics. If the number of documents
                    embedded are more, then the loading time of that particular document will be more.</p>

                <h2 id="/rest/guides/datastore/relations/props">Relationship Properties</h2>
                <p>
                    Datastore provides several useful features for documents in a relationship. Documents in a
                    relationship can have properties like an <TextHighlight>auto-updating</TextHighlight> feature for
                    the child and <TextHighlight>delete all</TextHighlight> of the children if parent document is
                    deleted.
                </p>
                <p>
                    These properties are defined under <TextHighlight>__rel</TextHighlight> key and it holds all the
                    properties required to define the
                    relationship between two documents. Following are the properties provided for defining the
                    relationship between documents.
                </p>

                <Table heads={["Property", "Details"]} data={this.relationshipProps}/>

                <h3>Type of relations</h3>

                <Table heads={["Relation", 'Details']} data={this.relations}/>

                <h3>Type of cascading</h3>

                <Table heads={['Cascade type', 'Details']} data={this.cascading}/>

                <Callout>
                    Cascading is only needed when the document is not being embedded.
                </Callout>

            </Section>
        );
    }
}



