import React from 'react';

import Menu from '../components/Menu';
import RestMenu from '../docs/platform/rest/RestMenu';

import RestGuides from '../docs/platform/rest/RestGuides';

import {BrowserRouter as Router, Route, Redirect} from 'react-router-dom';
import '../main.css';
import Scroller from './Scroller';

class App extends React.Component {

    render() {
        return (
            <Router>
                <Scroller>
                    <Menu>
                        <Route path="/docs/rest" component={RestMenu}/>
                    </Menu>
                    <div className='content'>
                        <Redirect from="/" to="/docs/rest/guides/start"/>
                        <Route path="/docs/rest/guides" component={RestGuides}/>
                    </div>
                </Scroller>
            </Router>
        );
    }

}
export default App;


