import React from 'react';
import Section from '../../../components/Section';
import Texthighlight from '../../../components/TextHighlight';
import Callout from '../../../components/Callout';
import Code from '../../../components/Code';
import * as code from '../../../resources/restcodes';

export default class OneToOne extends React.Component {

    render() {
        return (
            <Section>
                <h2 id="/rest/guides/datastore/relations/one-one">OneToOne</h2>
                <p>
                    To achieve <Texthighlight>One-to-One</Texthighlight> relationship in datastore, the document can
                    either be
                    <Texthighlight>embedded</Texthighlight> or can be <Texthighlight>linked</Texthighlight> as parent
                    and child document. For example, we are storing <Texthighlight>note</Texthighlight> and
                    <Texthighlight>content</Texthighlight> document. Each
                    note has content , following would be the ways to achieve this:
                </p>

                <h3>Embedding Document</h3>
                <p>
                    In One-To-One relationship, a document can be embedded inside a document either by sending a
                    document or referencing another document from database, below is the JSON format:
                </p>

                <Code>
                    {code.embedOneToOneJsonBody}
                </Code>

                <p>
                    Key content holds One-to-One relationship with the child document. Key __rel defines the properties
                    for the relationship between documents, and objects key holds the document to embed.
                </p>

                <Code>
                    {code.embedOneToOneCurl}
                </Code>

                <Callout>
                    Key <Texthighlight>objects</Texthighlight> hods single object for relationships One-to-One and
                    Many-to-One.
                </Callout>

                <p>
                    In the same way, a document already present in the database can be referenced to embed into the
                    parent document.
                </p>

                <Code>
                    {code.embeddingOneToOneReference}
                </Code>

                <Callout>
                    Here <Texthighlight>objects</Texthighlight> have only the <Texthighlight>_id</Texthighlight> key.
                    When referencing document already present in the database, do
                    not add other keys with <Texthighlight>_id</Texthighlight>, otherwise, instead of referencing that
                    document, datastore will update
                    document with the same <Texthighlight>_id</Texthighlight>.
                </Callout>

                <h3>Linking Document</h3>
                <p>
                    Linking documents result in storing the primary key of child document as a foreign key in the
                    parent document. Continuing the previous example, the sample JSON object would be as below:
                </p>

                <Code>
                    {code.linkingOneToOne}
                </Code>

                <p>
                    Property <Texthighlight>cascadeType</Texthighlight> <Texthighlight>all</Texthighlight> allows datastore to operate on child document for all CRUD operations.
                </p>
            </Section>
        )
    }
}



