import React from 'react';

import MenuSection from '../../../components/MenuSection';
import MenuList from '../../../components/MenuList';
import ListItem from '../../../components/ListItem';

export default class RestMenu extends React.Component {

    render() {
        return (
            <div>
                <MenuSection title="Guides" subTitle="Basics">
                    <MenuList>
                        <ListItem link="/rest/guides/start" text="Quick Start"/>
                        <ListItem>
                            <MenuList>
                                <ListItem link="/rest/guides/start/console" text="Developer Console"/>
                                <ListItem link="/rest/guides/start/docs" text="Documentation"/>
                            </MenuList>
                        </ListItem>
                        <ListItem link="/rest/guides/reference" text="Reference"/>
                        <ListItem link="/rest/guides/security" text="Security"/>
                        <ListItem>
                            <MenuList>
                                <ListItem link="/rest/guides/security/credentials" text="APIs & Credentials"/>
                                <ListItem link="/rest/guides/security/secret" text="Creating REST Secret"/>
                            </MenuList>
                        </ListItem>
                    </MenuList>
                </MenuSection>

                <MenuSection subTitle="Datastore">
                    <MenuList>
                        <ListItem link="/rest/guides/datastore" text="Intro"/>
                        <MenuList>
                            <ListItem link="/rest/guides/datastore/format" text="Document Format"/>
                            <ListItem link="/rest/guides/datastore/saving" text="Saving Document"/>
                        </MenuList>
                        <ListItem link="/rest/guides/datastore/relations" text="Relations"/>
                        <ListItem>
                            <MenuList>
                                <ListItem link="/rest/guides/datastore/relations/props" text="Relationship Properties"/>
                                <ListItem link="/rest/guides/datastore/relations/one-one" text="OneToOne"/>
                                <ListItem link="/rest/guides/datastore/relations/one-many" text="OneToMany"/>
                                <ListItem link="/rest/guides/datastore/relations/many-one" text="ManyToOne"/>
                                <ListItem link="/rest/guides/datastore/relations/many-many" text="ManyToMany"/>
                            </MenuList>
                        </ListItem>
                        <ListItem link="/rest/guides/datastore/query" text="Querying Documents"/>
                        <ListItem>
                            <MenuList>
                                <ListItem link="/rest/guides/datastore/query/id" text="By _id"/>
                                <ListItem link="/rest/guides/datastore/query/ops" text="Operators"/>
                                <ListItem link="/rest/guides/datastore/query/basic" text="Basic Query"/>
                                <ListItem link="/rest/guides/datastore/query/criteria" text="Query Criteria"/>
                                <ListItem link="/rest/guides/datastore/query/unique" text="Unique Document"/>
                                <ListItem link="/rest/guides/datastore/query/projections" text="Projections"/>
                                <ListItem link="/rest/guides/datastore/query/sorting" text="Sorting Documents"/>
                                <ListItem link="/rest/guides/datastore/query/joins" text="Joins"/>
                                <ListItem link="/rest/guides/datastore/query/logical-ops" text="Logical Operators"/>
                                <ListItem link="/rest/guides/datastore/query/embed" text="Querying Embedded Documents"/>
                            </MenuList>
                        </ListItem>

                        <ListItem link="/rest/guides/datastore/update" text="Updating Documents"/>
                        <ListItem>
                            <MenuList>
                                <ListItem link="/rest/guides/datastore/update/arrays" text="Updating Arrays"/>
                                <ListItem link="/rest/guides/datastore/update/relations" text="Update Relations"/>
                                <ListItem link="/rest/guides/datastore/update/embed" text="Update Embed Document"/>
                                <ListItem link="/rest/guides/datastore/update/embed/values" text="Update Values of Embed Document"/>
                                <ListItem link="/rest/guides/datastore/update/link" text="Update Linked Document"/>
                                <ListItem link="/rest/guides/datastore/update/keys" text="Delete Keys"/>
                            </MenuList>
                        </ListItem>
                        <ListItem link="/rest/guides/datastore/delete/document" text="Delete Document"/>
                    </MenuList>
                </MenuSection>
            </div>
        )
    }
}




