import React from 'react';
import Section from '../../../components/Section';
import TextHighlight from '../../../components/TextHighlight';
import Code from '../../../components/Code';
import * as codes from '../../../resources/restcodes';
import Callout from '../../../components/Callout';

export default class OneToMany extends React.Component {

    render() {
        return (
            <Section>
                <h2 id="/rest/guides/datastore/relations/one-many">OneToMany</h2>
                <p>
                    A <TextHighlight>One-to-Many</TextHighlight> relationship is similar to One-to-One and can be
                    achieved in the same way. From the
                    previous example, a single user can have many notes, so the relationship between notes and user
                    document is <TextHighlight>One-to-Many</TextHighlight>.
                </p>

                <Code>
                    {codes.oneToManyJson}
                </Code>
                <p>
                    Notice child document <TextHighlight>notes</TextHighlight> has its own children, in the same way,
                    documents can have nested child.
                </p>

                <Callout>
                    Here <TextHighlight>objects</TextHighlight> key has a collection of
                    <TextHighlight>objects</TextHighlight>, that is because objects
                    holds documents for <TextHighlight>many</TextHighlight> side of the relationship.
                </Callout>
            </Section>
        )
    }
}



