import React from 'react';
import Section from '../../../components/Section';
import TextHighlight from '../../../components/TextHighlight';
import Code from '../../../components/Code';
import * as codes from '../../../resources/restcodes';
import {Link} from 'react-router-dom';
import Table from '../../../components/Table';
import Callout from '../../../components/Callout';

export default class Querying extends React.Component {

    comparisionOperators = [
        ['$eq', 'Performs equal to operation'],
        ['$ne', 'Performs not equal to operation'],
        ['$gt', 'Performs greater than operation'],
        ['$gte', 'Performs greater than or equal to operation'],
        ['$lt', 'Performs less than operation'],
        ['$lte', 'Performs less than or equal to operation'],
        ['$in', 'Performs matches for any of the value specified in the array'],
        ['$nin', 'Performs matches for none of the values specified in the array'],
        ['$exists', 'Performs matches for documents that have specified key'],
        ['$size', 'Performs matches for documents that have an array field of specified size']
    ];


    logicalOperators = [
        ['where', 'Starts off the query, and returns the document that fulfils the specified criteria'],
        ['and', <div>Joins multiple criteria with logical <TextHighlight>AND</TextHighlight>, and returns all the
            documents that fulfil the specified criteria</div>],
        ['or',
            <div>Joins multiple criteria with logical <TextHighlight>OR</TextHighlight>, and returns all the documents
                that fulfil any of the specified criteria</div>],
        ['notSelect', 'Returns all the documents that do not match the query'],
        ['norSelect',
            <div>Joins multiple criteria with logical <TextHighlight>OR</TextHighlight>, and returns all the documents
                that do not match both the clauses</div>]
    ];

    elementOperators = [
        ['orderBy', 'Orders the document in the specified format'],
        ['projections', 'Projects the specified keys of a document'],
        ['findUnique', 'Finds and returns a unique document matching the query'],
        ['joins', 'Normalizes and returns documents with their child'],
        ['limit', 'Limits the count of documents returned']
    ];

    render() {
        return (
            <Section>
                <h1 id="/rest/guides/datastore/query">Querying Document</h1>
                <p>
                    After the document has been saved, you can retrieve the document by sending a
                    &nbsp;<TextHighlight>GET</TextHighlight> request to
                    datastore URL with the <TextHighlight>_id</TextHighlight> of the document you want: For example to
                    retrieve the document which
                    weve saved in an earlier example:
                </p>
                <h2 id="/rest/guides/datastore/query/id">Query by _id</h2>
                <Code>
                    {codes.queryById}
                </Code>

                <p>
                    The response body sent contains the JSON object containing all key-value pairs stored.
                </p>

                <Code>
                    {codes.queryByIdResponse}
                </Code>

                <p>
                    Notice key <TextHighlight>content</TextHighlight> and <TextHighlight>createdBy</TextHighlight> has a
                    hexadecimal value. These values are the <TextHighlight>_ids</TextHighlight> of child
                    document linked with this document.
                </p>
                <p>
                    When retrieving a document you can also retrieve its child document in a single request. Look
                    for <Link to="/rest/guides/datastore/query/joins">Joins</Link> in documentation.
                </p>

                <p>
                    Datastore allows you to retrieve documents using query. The query can have multiple criteria and
                    datastore matches the documents against the query sent in the request. Listed below are the
                    operators supported by datastore:
                </p>

                <h2 id="/rest/guides/datastore/query/ops">Operators</h2>
                <p>
                    Following are the operators supported by <TextHighlight>Datastore</TextHighlight>.
                </p>

                <h3>Comparision Operator</h3>
                <Table heads={['Operators', 'Details']} data={this.comparisionOperators}/>
                <h3>Logical Operator</h3>
                <Table heads={['Operators', 'Details']} data={this.logicalOperators}/>
                <h3>Element Operator</h3>
                <Table heads={['Operators', 'Details']} data={this.elementOperators}/>

                <p>
                    <TextHighlight>Element Operators</TextHighlight> are always placed on the top level of the JSON
                    body, i.e., where you place the
                    <TextHighlight>Logical Operator</TextHighlight> <TextHighlight>where</TextHighlight>. Logical
                    operators hold the comparison operators for selecting the document,
                    is why you should never put any element operator inside a logical operator because it will not
                    work.
                </p>

                <h2 id="/rest/guides/datastore/query/basic">Basic Query</h2>
                <p>
                    Basic query is used when you want to retrieve multiple documents at once without any criteria,
                    simply
                    send a <TextHighlight>GET</TextHighlight> request to datastore URL without any URL parameter, this
                    will list all documents stored
                    in datastore.
                </p>

                <Code>
                    {codes.basicQuery}
                </Code>

                <p>The return value is a JSON array containing all the documents stored with the name note.</p>

                <Code>
                    {codes.basicQueryResponse}
                </Code>

                <h2 id="/rest/guides/datastore/query/criteria">Query Criteria</h2>
                <p>
                    Query Criteria allows datastore to restrict document search based on criteria in the query, using
                    the
                    <TextHighlight>q</TextHighlight> URL parameter. The simplest form of query criteria would be
                    restricting search based on <TextHighlight>key value</TextHighlight>.
                    For example, if we want to retrieve a <TextHighlight>note</TextHighlight> with
                    <TextHighlight>date</TextHighlight> <TextHighlight>equal</TextHighlight> to <TextHighlight>January
                    15, 2016</TextHighlight>, and <TextHighlight>remind</TextHighlight>
                    <TextHighlight>equals</TextHighlight> to
                    <TextHighlight>false</TextHighlight>:
                </p>

                <Code>{codes.queryCriteria}</Code>
                <p>The return value is a JSON array containing documents those matches query.</p>

                <h2 id="/rest/guides/datastore/query/unique">Unique Document</h2>
                <p>
                    Previously performed queries have returned a collection of documents, but what if we want datastore
                    to return a <TextHighlight>unique</TextHighlight> document that matches the query?
                    Simply add <TextHighlight>findUnique</TextHighlight> <TextHighlight>Element Operator</TextHighlight>
                    in the query and datastore will return the first document that matches the query. For example:
                </p>

                <Code>
                    {codes.uniqueDocument}
                </Code>

                <p>
                    As you can see, the return value is a unique document that matches the query.
                </p>

                <Code>
                    {codes.uniqueDocumentResponse}
                </Code>

                <h2 id="/rest/guides/datastore/query/projections">Projections</h2>
                <p>
                    With projections, you can restrict the keys returned by datastore. To retrieve a document that
                    contains only the <TextHighlight>date</TextHighlight> and <TextHighlight>title</TextHighlight> key
                    (built in keys like <TextHighlight>_id</TextHighlight> are always returned and can never be
                    excluded):
                </p>
                <Code>
                    {codes.projection}
                </Code>
                <p>As you can see, response contain the only the keys we wanted.</p>
                <Code>
                    {codes.projectionResponse}
                </Code>

                <h2 id="/rest/guides/datastore/query/sorting">Sorting Documents</h2>
                <p>
                    Element Operator <TextHighlight>orderBy</TextHighlight> is used to sort the result-set by one or
                    more key(s). Documents could be
                    ordered by key in an <TextHighlight>ASC</TextHighlight> or <TextHighlight>DESC</TextHighlight>
                    (Ascending or Descending) order. Below is the format for adding
                    sort order in query:
                </p>
                <Code>
                    {codes.sortingDocuments}
                </Code>

                <h2 id="/rest/guides/datastore/query/joins">Joins</h2>
                <p>
                    When retrieving a document, if it contains references to another document, the referenced document
                    can also be retrieved in the same request using <TextHighlight>joins</TextHighlight>. Joins allow
                    joining different documents on
                    keys which hold a reference to another document.
                </p>

                <p>
                    When joining documents you would also like to apply <TextHighlight>projections</TextHighlight> on
                    child document, <TextHighlight>joins</TextHighlight> allows you
                    to define projections inside the <TextHighlight>join criteria</TextHighlight> but not on the
                    documents which are embedded. Creating
                    joins in datastore is easy; simply add <TextHighlight>joins</TextHighlight> operator in the query
                    and the rest is handled by
                    datastore.
                </p>

                <p>
                    For example, retrieving a <TextHighlight>note</TextHighlight> with
                    <TextHighlight>date</TextHighlight> <TextHighlight>equal</TextHighlight> to <TextHighlight>January
                    15 2016</TextHighlight>, and
                    performing <TextHighlight>joins</TextHighlight> on key
                    <TextHighlight>createdBy</TextHighlight> and projecting only <TextHighlight>username</TextHighlight>
                    key:
                </p>

                <Code>
                    {codes.joins}
                </Code>

                <p>
                    Operator <TextHighlight>joins</TextHighlight> is an array of HashMap in which each hashmap contains
                    <TextHighlight>key</TextHighlight> and <TextHighlight>projections</TextHighlight> key. The
                    value of the <TextHighlight>key</TextHighlight> is a key on which join is to be made,
                    <TextHighlight>projections</TextHighlight> value is the collection of keys
                    you want datastore to return for child document. Remember that the key on which you are performing
                    join must have a relationship.
                </p>

                <h2 id="/rest/guides/datastore/query/logical-ops">Logical Operators</h2>
                <p>
                    Datastore provides several logical operators to query documents. Logical operators allow defining
                    multiple criteria and datastore matches the documents fulfilling the operator and the query
                    criteria.
                </p>

                <p>
                    Retrieving previously saved documents, we are querying all the documents which either have a value
                    of key <TextHighlight>remind</TextHighlight> as <TextHighlight>true</TextHighlight> or
                    <TextHighlight>false</TextHighlight>. The same can be achieved using the
                    <TextHighlight>OR</TextHighlight> logical operator in the query, as shown in the sample query below:
                </p>

                <Code>{codes.logicalOperators}</Code>
                <Code>
                    {codes.logicalOperatorsResponse}
                </Code>

                <p>
                    Other logical operators can be used in the same way as shown above, and the result will be affected
                    accordingly. Element operators can also be used with logical operators if needed.
                </p>

                <p>
                    When using a logical operator to retrieve documents, datastore returns the collection of documents,
                    since it performs matches on multiple documents. If the element operator
                    <TextHighlight>findUnique</TextHighlight> is applied datastore will return the first document
                    matching the query which is not the expected result when
                    using Logical Operators.
                </p>

                <h2 id="/rest/guides/datastore/query/embed">Querying Embedded Documents</h2>
                <p>
                    To specify a condition on a field in an embedded document, use the <TextHighlight>dot
                    notation</TextHighlight>. For example, retrieving a document <TextHighlight>notes</TextHighlight>
                    where the value of <TextHighlight>tag</TextHighlight> key in the
                    <TextHighlight>content</TextHighlight> document equals <TextHighlight>important</TextHighlight>.
                </p>

                <Code>
                    {codes.queryEmbedDocument}
                </Code>

                <Callout>
                    In the query, <TextHighlight>content.tag</TextHighlight> is used to target the embedded document
                    key.
                </Callout>

                <Code>
                    {codes.queryEmbedDocumentResposne}
                </Code>
            </Section>
        )
    }
}



