import React from 'react';
import Section from '../../../components/Section';
import TextHighlight from '../../../components/TextHighlight';
import Code from '../../../components/Code';
import * as codes from '../../../resources/restcodes';
import Table from '../../../components/Table';


export default class UpdatingDocument extends React.Component {

    arrayUpdateOperators = [
        ['append', 'Appends a given array of objects to the end of an array field'],
        ['pop', 'Removes all the instances of each given object from an array field']
    ];

    render() {

        return (
            <Section>
                <h1 id="/rest/guides/datastore/update">Updating Documents</h1>
                <p>
                    A <TextHighlight>PUT</TextHighlight> request to datastore URL and a project can update a previously
                    saved document. The JSON object
                    format is the same as used for saving the document. Keys you dont specify will remain unchanged, so
                    you can update a subset of the document, and if theres any key which is not there in the document
                    will be added.
                </p>
                <p>
                    Document to be updated can be targeted either with their <TextHighlight>_id</TextHighlight> or can
                    be updated using a <TextHighlight>query</TextHighlight>.
                </p>

                <p>
                    Updating a previously saved document <TextHighlight>notes</TextHighlight> by its
                    <TextHighlight>_id</TextHighlight> and changing the value of key
                    <TextHighlight>title</TextHighlight> and adding
                    new key <TextHighlight>list</TextHighlight>.
                </p>
                <Code>
                    {codes.updatingDocumentJson}
                </Code>

                <Code>
                    {codes.updatingDocumentRequest}
                </Code>

                <p>
                    After updating, the document in the database will have a new value in the key
                    <TextHighlight>title</TextHighlight> and a new key <TextHighlight>list</TextHighlight> is added.
                </p>

                <h2 id="/rest/guides/datastore/update/arrays">Updating Arrays</h2>
                <p>
                    To help with updating array data, there are two operations that can be used:
                </p>


                <Table heads={['Keys', 'Description']} data={this.arrayUpdateOperators}/>

                <p>
                    Each operation takes an array of objects to add or remove in the objects key. For example, we can
                    add an item to the set, like "Shades" which may appear as:
                </p>

                <Code>
                    {codes.updateArraysJsonBody}
                </Code>

                <Code>
                    {codes.updateArrayRequest}
                </Code>

                <h2 id="/rest/guides/datastore/update/relations">Updating Relations</h2>
                <p>
                    When updating any document, <TextHighlight>embedded</TextHighlight> documents and <TextHighlight>referenced</TextHighlight>
                    documents can also be updated in the
                    same request. Datastore looks for the <TextHighlight>cascade</TextHighlight> property when updating
                    the child document. If the
                    document has been <TextHighlight>embedded</TextHighlight>, then the cascading has no effect and the
                    document would be updated.
                </p>

                <h2 id="/rest/guides/datastore/update/embed">Updating Embed Document</h2>
                <p>
                    When updating embed document, keys you dont specify will remain unchanged, and if any key which is
                    not there in the document will be added. For example, updating <TextHighlight>notes</TextHighlight>
                    document where we are updating value of <TextHighlight>tag</TextHighlight> key to <TextHighlight>urgent</TextHighlight>:
                </p>

                <Code>
                    {codes.updateEmbedDocument}
                </Code>

                <p>
                    Operations used to update the array data can also be used to update the array of <TextHighlight>embedded
                    documents</TextHighlight>.
                    If no operation is mentioned while updating the array of documents, then a new array of documents
                    from <TextHighlight>objects</TextHighlight> will be inserted in the same array:
                </p>

                <Code>
                    {codes.updatingArrayEmbed}
                </Code>

                <p>
                    In the same way, you can remove a document from the collection matching the value of each key in
                    objects.
                </p>

                <Code>
                    {codes.removeArrayEmbed}
                </Code>

                <h2 id="/rest/guides/datastore/update/embed/values">Updating Values of Embed Document</h2>
                <p>
                    What if you want to update an existing embedded document? You can do that by sending a query that
                    matches the embedded document you want to update and data along with it. For example, here we are
                    updating an embedded document whose <TextHighlight>date</TextHighlight> key under document
                    <TextHighlight>notes</TextHighlight> must have value <TextHighlight>equal</TextHighlight> to
                    <TextHighlight>12
                        February 2015</TextHighlight>. Notice <TextHighlight>query</TextHighlight> uses <TextHighlight>dot
                    notation</TextHighlight> to match the embed document.
                </p>

                <Code>
                    {codes.updateEmbedDocumentValues}
                </Code>

                <h2 id="/rest/guides/datastore/update/link">Update Linked Document</h2>
                <p>
                    Updating linked documents requires the <TextHighlight>_id</TextHighlight> key present in the JSON
                    body matching the document you
                    want to update. If the relationship between documents is of type
                    <TextHighlight>Many-to-Many</TextHighlight>, datastore requires
                    you to send the <TextHighlight>_id</TextHighlight> of <TextHighlight>parent document</TextHighlight>
                    in JSON body otherwise the child document will not be
                    updated. For example, here we are updating one of the <TextHighlight>book</TextHighlight> documents
                    linked with the <TextHighlight>author</TextHighlight> document
                    with a Many-to-Many relationship. Child document which we want to update has the
                    <TextHighlight>_id</TextHighlight> key present in
                    JSON body.
                </p>

                <Code>
                    {codes.updateLinkedDocument}
                </Code>

                <p>
                    In the same way, you can update child documents with any kind of relationships.
                </p>

                <p>
                    When the relationship between documents is of <TextHighlight>One-to-Many</TextHighlight> or
                    <TextHighlight>Many-to-Many</TextHighlight>, you may want to add more
                    children to the parent document. You can achieve that by sending a
                    <TextHighlight>PUT</TextHighlight> request to the document with
                    the request body same as the saving document and the rest would be handled by datastore. For
                    example, we are adding a <TextHighlight>note</TextHighlight> to <TextHighlight>users</TextHighlight>
                    document:
                </p>

                <Code>
                    {codes.oneToManyAndManyToManyUpdate}
                </Code>

                <h2 id="/rest/guides/datastore/update/keys">Delete Keys</h2>

                <p>
                    You can also delete keys from the document. When deleting keys, all documents having the key will be
                    affected and data for these will also be deleted. For example, we are deleting the keys
                    &nbsp;<TextHighlight>remind</TextHighlight> and <TextHighlight>date</TextHighlight> from all
                    documents:
                </p>

                <Code>
                    {codes.deleteKey}
                </Code>

                <p>
                    Response will be 200 OK and contains JSON body with key <TextHighlight>modified</TextHighlight>
                    mentioning the number of document
                    affected with the request.
                </p>

                <Code>
                    {codes.deleteKeyResponse}
                </Code>
            </Section>
        )
    }
}



