import React from 'react';


export default class Table extends React.Component {

    renderTableHeads() {
        let heads = this.props.heads;

        let th = heads.map((head, i) => {
            return (
                <th key={i}>{head}</th>
            )
        });

        return th;
    }

    renderTableRows() {
        let rows = this.props.data;

        let rowCount = 0;

        return rows.map((row, i) => {

            let tds = row.map((data) => {
                ++rowCount;

                return <td key={rowCount}>{data}</td>
            });

            return <tr key={i}>{tds}</tr>
        });
    }

    render() {
        return (
            <table>
                <thead>
                <tr>
                    {this.renderTableHeads()}
                </tr>
                </thead>
                <tbody>
                {this.renderTableRows()}
                </tbody>
            </table>
        )
    }
}



