import React from 'react';
import Section from '../../../components/Section';
import TextHighlight from '../../../components/TextHighlight';
import Code from '../../../components/Code';
import * as codes from '../../../resources/restcodes';

export default class ManyToMany extends React.Component {


    render() {
        return (
            <Section>
                <h2 id="/rest/guides/datastore/relations/many-many">ManyToMany</h2>
                <p>
                    In this relationship, <TextHighlight>many</TextHighlight> <TextHighlight>parent
                    documents</TextHighlight> can be referenced with <TextHighlight>many</TextHighlight>
                    <TextHighlight>child documents</TextHighlight>, and the
                    references between child and parent documents are stored in another document referred to as
                    <TextHighlight>joinDocument</TextHighlight>. <TextHighlight>joinDocument</TextHighlight> stores
                    <TextHighlight>_id</TextHighlight> of <TextHighlight>parent document</TextHighlight> under the
                    key <TextHighlight>joinKey</TextHighlight> and <TextHighlight>_id</TextHighlight> of
                    <TextHighlight>child document</TextHighlight> under the key
                    <TextHighlight>inverseJoinDocument</TextHighlight>.
                </p>
                <p>
                    For example, in a project which stores <TextHighlight>books</TextHighlight> and <TextHighlight>authors</TextHighlight>
                    details, many books could have many authors. Following would be the sample JSON object to achieve
                    the same.
                </p>

                <Code>
                    {codes.manyToManyJson}
                </Code>

                <p>
                    As you can see that <TextHighlight>Jordan</TextHighlight> has written two books, these books could
                    later also be referred to other
                    <TextHighlight>authors</TextHighlight> and the join relationship would be stored in the document
                    <TextHighlight>AuthorBooks</TextHighlight>.
                </p>

                <p>
                    <TextHighlight>Embedding</TextHighlight> cannot be achieved in a Many-to-Many relationship. If
                    applied, it will be ignored. Also,
                    do not make this relationship inside a document which is embedded inside another document, if did
                    then child documents holding <TextHighlight>Many-to-Many</TextHighlight> relationship will not be
                    updated since their parent
                    document is embedded in another document and has no <TextHighlight>_id</TextHighlight>.
                </p>
            </Section>
        );
    }
}



